@font-face {
    font-family: 'Adieu';
    src: url('../fonts/Adieu-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: 'Adieu';
    src: url('../fonts/Adieu-Bold.woff2') format('woff2'),
         url('../fonts/Adieu-Bold.woff') format('woff'),
         url('../fonts/Adieu-Bold.otf') format('opentype'),
         url('../fonts/Adieu-Bold.eot') format('embedded-opentype');
    font-weight: 700;
    font-style: normal;
}

.font-display {
    font-family: 'Adieu', sans-serif;
    font-weight: 700;
}

/* font-size */

.font-xxs {
    font-size: 0.625rem; /* 10px */
    line-height: 1.25;
}
.font-xs {
    font-size: 0.75rem; /* 12px */
    line-height: 1.25;
}
.font-sm {
    font-size: 0.875rem; /* 14px */
}
.font-md {
    font-size: 1rem; /* 16px */
}
.font-lg {
    font-size: 1.25rem; /* 20px */
}
.font-xl {
    font-size: 1.5rem; /* 24px */
}
.font-xxl {
    font-size: 2rem; /* 32px */
}

/* font-weight */
.font-normal {
    font-weight: 400;
}
.font-bold {
    font-weight: 700;
}
/* align */
.text-left {
    text-align: left !important;
}
.text-center {
    text-align: center !important;
}
.text-right {
    text-align: right !important;
}